.update-task-page {
  min-height: 100vh;
  background: linear-gradient(135deg, rgba(0, 184, 148, 0.1) 0%, rgba(0, 206, 201, 0.1) 100%);
  background-image: url('/public/images/update-task-bg.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  padding: 4rem 2rem;
  position: relative;
}

.update-task-container {
  max-width: 800px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 2.5rem;
  position: relative;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation: slideUp 0.5s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.update-task-header {
  text-align: center;
  margin-bottom: 2.5rem;
  position: relative;
}

.update-task-icon {
  font-size: 3rem;
  color: #00b894;
  margin-bottom: 1rem;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.update-task-header h2 {
  font-size: 2.2rem;
  color: #2d3436;
  margin-bottom: 1rem;
  font-weight: 700;
  background: linear-gradient(135deg, #00b894 0%, #00cec9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.update-task-header p {
  color: #636e72;
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

.update-task-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  position: relative;
}

.form-group label {
  font-size: 1rem;
  color: #2d3436;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.input-icon {
  color: #00b894;
  font-size: 1.1rem;
}

.form-group input,
.form-group textarea {
  padding: 0.8rem 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: rgba(248, 250, 252, 0.8);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #00b894;
  box-shadow: 0 0 0 3px rgba(0, 184, 148, 0.1);
  background: white;
}

.form-group textarea {
  min-height: 120px;
  resize: vertical;
}

.update-task-form button {
  margin-top: 1rem;
  margin-right: 4rem;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #00b894 0%, #00cec9 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.2);
}

.update-task-form button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 184, 148, 0.3);
}

.update-task-form button:disabled {
  background: #cbd5e0;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.error-message {
  color: #ff4757;
  font-size: 0.9rem;
  margin-top: 0.3rem;
}

@media (max-width: 768px) {
  .update-task-page {
    padding: 2rem 1rem;
  }

  .update-task-container {
    padding: 1.5rem;
  }

  .update-task-header h2 {
    font-size: 1.8rem;
  }

  .update-task-header p {
    font-size: 1rem;
  }

  .update-task-form button {
    width: 100%;
    padding: 0.8rem 1.5rem;
  }
}
